<script>
	import P5Canvas from "./P5Canvas.svelte"

	let sketch = function(p5) {
	  let w;
	  let columns;
	  let rows;
	  let board;
	  let next;

	  p5.setup = () => {
			p5.createCanvas(600, 600)
			p5.angleMode(p5.DEGREES)
		}

	  p5.draw = () => {
			p5.background(255)
			p5.stroke(255,0,0)
			p5.strokeWeight(50)
			p5.arc(200,200,200,200,60,60+180)
	  }
	}
</script>

<style>
  main {
    font-family: sans-serif;
    text-align: center;
  }
</style>

<main>
  <P5Canvas sketch={sketch}/>
</main>